import { useAtom } from "jotai"
import { atomBriefWatchlistFilter } from "@rp/react/common/brief"

export function useWatchlistSearch() {
  const [filterTerm, setFilterTerm] = useAtom(atomBriefWatchlistFilter)

  const isSearching = filterTerm.trim().length > 0

  return {
    filterTerm,
    setFilterTerm,
    isSearching,
  }
}
